const galleryListEl = document.querySelector(".gallery");


const galleryTotal = images.map(img => {
    const ItemEl = document.createElement("li");
    ItemEl.classList.add("gallery-item");

    const linkEl = document.createElement("a");
    linkEl.classList.add("gallery-link");
    linkEl.href = img.original;

    const imageEl = document.createElement("img");
    imageEl.classList.add("gallery-image");
    imageEl.src = img.preview;
    imageEl.dataset.source = img.original;
    imageEl.alt = img.description;
    imageEl.width = 360;
    imageEl.height = 200;

    linkEl.append(imageEl);
    ItemEl.append(linkEl);
    return ItemEl
})
galleryListEl.append(...galleryTotal);

console.log(galleryListEl);

const simplelightbox = new SimpleLightbox('.gallery .gallery-link', {
    captionsData: 'alt',
    captionPosition: 'bottom',
    captionDelay: 250,
});

